import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { MatIconModule } from '@angular/material/icon';
import { MatExpansionModule } from '@angular/material/expansion';
import { CommonModule, NgForOf, NgIf, NgTemplateOutlet } from '@angular/common';
import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { PortalModule } from '@angular/cdk/portal';
import { DscAccordionItem } from './shared/dsc-accordion-item';

@Component({
  selector: 'dsc-accordion',
  standalone: true,
  imports: [MatIconModule, MatExpansionModule, NgIf, PortalModule, NgTemplateOutlet, NgForOf, CommonModule],
  encapsulation: ViewEncapsulation.None,
  templateUrl: './dsc-accordion.component.html',
  styleUrls: ['./dsc-accordion.component.scss']
})
export class DscAccordionComponent {
  @Input()
  get multi(): boolean {
    return this._multi;
  }

  set multi(value: boolean | string) {
    this._multi = coerceBooleanProperty(value);
  }

  private _multi = false;

  @Input()
  accordionItems?: DscAccordionItem[];

  @Input() iconStyle: 'outlined' | 'filled' | 'rounded' | 'sharp' | 'two-tone' = 'filled';

  @Input() sizeTitle: 'standard' | 'small' | 'large' = 'standard';

  @Output()
  itemExpanded: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  afterCollapse: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  afterExpand: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  closed: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  opened: EventEmitter<void> = new EventEmitter<void>();

  onClick(item: any, $event: any): void {
    this.itemExpanded.emit(item);
    $event.stopPropagation();
  }

  getIconClass(): string {
    if (this.iconStyle === 'filled') return 'material-icons';
    if (this.iconStyle === 'rounded') return 'material-icons-round';
    return `material-icons-${this.iconStyle}`;
  }

  getTitleSizeClass(): string {
    switch(this.sizeTitle){
      case 'small': return 'dsc-accordion__title--small';
      case 'large': return 'dsc-accordion__title--large';
      default: return 'dsc-accordion__title--standard';
    }
  }
}
